import React from 'react'
import style from './style.scss'
import { Link } from 'react-router-dom'
import InfoQuery from '@/Components/InfoQuery'

let container = (props) => {
  if (props.isLoading) {
    return null
  }
  let {
    id,
    title,
    images: { large: image },
    directors,
    casts,
    genres,
    durations,
    mainland_pubdate
  } = props.data
  return (
    <div className={style.wrapper}>
      <p>
        <Link to={`/subject/${id}`} >{`>  ${title} `}</Link>
      </p>
      <img className={style.img}src={image} />
      <InfoQuery query="" value={directors} />
      <InfoQuery query="" value={casts} />
      <InfoQuery query="" value={genres} />
      <InfoQuery query="" value={durations} />
      <InfoQuery query="" value={mainland_pubdate} />

    </div>
  )
}

export default container


