import React from 'react'
import style from '../css/main.scss'
import InfoQuery from '@/Components/InfoQuery'


export default (props) => {
  let { gender, constellation, birthday, born_place, professions, aka_en, aka } = props.data
  return (
    <div className={style.infoPanel}>
      <InfoQuery query="" value={gender} />
      <InfoQuery query="" value={constellation} />
      <InfoQuery query="" value={birthday} />
      <InfoQuery query="" value={born_place} />
      <InfoQuery query="" value={professions} />
      <InfoQuery query="" value={aka_en} />
      <InfoQuery query="" value={aka} />
    </div>
  )
}


